/* Fonts */
@font-face {
    font-family: 'HelveticaNeueLT Condensed';
    src: url('/src/fonts/HelveticaNeueLT-Condensed.otf');
}

@font-face {
    font-family: 'HelveticaNeueLT LightCond';
    src: url('/src/fonts/HelveticaNeueLT-LightCond.otf');
}

@font-face {
    font-family: 'HelveticaNeue Regular';
    src: url('/src/fonts/HelveticaNeue-Regular.ttf');
}

@font-face {
    font-family: 'Helios Cond';
    src: url('/src/fonts/Helios-Cond.ttf');
}

@font-face {
    font-family: 'HeraldSquareNF';
    src: url('/src/fonts/HeraldSquareNF.ttf');
}

/**** Settings ****/
body {
    margin: 0;
    padding: 0;

    font-family: 'HelveticaNeueLT Condensed';
}

*,
*:after,
*:before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

.container {
    width: 100%;
    max-width: 1380px;
    padding: 0 15px;
    margin: 0 auto;
}

/**** Header Top ****/
.header__social {
    background-color: #1b1f20;
}

.social {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 20px 0;
}

.social__contacts {
    display: flex;

    font-size: 16px;
    color: #ffffff;
}

.contacts__item {
    position: relative;
    padding-left: 25px;
}

.contacts__item:first-child {
    margin-right: 20px;
}

.contacts__icon {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}

.icons__link {
    margin-left: 15px;

    text-decoration: none;
}

.icons__link:first-child {
    margin-left: 0;
}

/*.icons__link:hover {
    box-shadow: ;
}*/

/****Header__menu****/
.header__menu {
    background-color: #ffffff;
}

.menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 12px 0;
}

.nav__link {
    margin-left: 45px;

    font-size: 22px;
    color: #1b1f20;

    text-transform: uppercase;
    text-decoration: none;
    transition: color 0.2s linear;
}

.nav__link:first-child {
    margin-left: 0;
}

.nav__link:hover {
    color: #277db3;
}

/**** Header__inner ****/
.header__welcome {
    padding: 221px 0 500px;

    background: url('../img/intro.jpg') no-repeat center;
    background-size: cover;
}

.welcome {
    display: flex;
    justify-content: flex-end;
}

.welcome__item {
    width: 100%;
    max-width: 480px;
}

.welcome__title {
    font-size: 122px;
    font-weight: 400;
    color: #ffffff;

    text-transform: uppercase;
}

.welcome__subtitle {
    margin-bottom: 15px;

    font-size: 65px;
    font-weight: 400;
    color: #ffffff;

    text-transform: uppercase;
}

.welcome__text {
    font-size: 18px;
    color: #ffffff;
    line-height: 1.3;
}

/****About ****/
.section__about {
    padding: 35px 0;

    background: url('../img/bg-beige.svg') no-repeat;
    background-size: cover;
}

.about {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; 
}

.about__item {
    width: 40%;
}

.about__item:last-child {
    width: 48%;
}

.item__link {
    display: block;

    font-size: 0;
    color: transparent;
}
.link {
    width: 72px;
    height: 6px;
    
    background-color: #fae5d3;

    margin-bottom: 14px;  
}

.link__sub {
    width: 100%;
    height: 2px;

    background-color: #fae5d3;

    margin-bottom: 37px;
}

.about__prof {
    font-family: 'HelveticaNeueLT LightCond';
    font-size: 60px;
    font-weight: 400;
    color: #fae5d3;
    line-height: 1.3;

    text-transform: uppercase;
}

.about__title {
    font-family: 'HelveticaNeue Regular';
    font-size: 63px;
    color: #ffffff;
    line-height: 1;

    text-transform: uppercase;
    letter-spacing: 10px;

    margin-bottom: 68px;
}

.about__text {
    font-family: 'HelveticaNeue Regular';
    font-size: 18px;
    color: #3c3d3e;
    line-height: 1.6;

    margin-bottom: 55px;
}

.about__btn {
    padding: 15px 35px;

    font-family: 'Helios Cond';
    font-size: 18px;
    color: #ffffff;

    text-transform: uppercase;
    text-decoration: none;

    background-color: #277db3;
}

/**** Gallery ****/
.section__gallery {
    padding: 35px 0;

    background: #f0f1f2;
}

.link--color {
    background-color: #79777a;
}
